/* HTML TAGS */
h1{
    font-size: 3.6rem;
}

nav p {
    font-size: 2rem;
}

article p{
    font-size: 3.6rem;
    margin-top: 8rem;
}

article{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 4rem;
}
/* CLASSES */

/* NAVIGATION MENU */
.nav-menu{
    display: flex;
    gap: 4rem;
    justify-content: space-between;
    align-items: center;
}

.nav-logo{
    width: 24rem;
    margin: 2rem auto 2rem 2rem;
}

.shopping-cart{
    width: 3rem;
    margin-right: 4rem;
}

.main-title{
    margin-right: auto;
}

